<template>
<div>
    <div v-if="formType === 'my-profile' || formType === 'change-password'" class="large-12 cell">
        <!-- profile form -->
        <div class="grid-container-full">
          <div class="grid-x grid-padding-x">
                <form id="userFormId" name="userForm"
                      class="large-5 medium-6 small-12 cell"
                      action="#" 
                      data-abide
                      data-live-validate="true"
                      novalidate onsubmit="event.preventDefault()"
                      v-on:submit="bindValues">
                    <ins-card steady>
                      <div class="grid-x grid-padding-x"> 
                        <div class="large-12 cell">
                        <h4 class="ins-card-heading">User Details</h4>
                          <InsInputWrap
                            id="userFirstNameField"
                            name="userFirstNameField" 
                            placeholder="First Name"  
                            label="First Name*" 
                            field="text"            
                            autocomplete="off"
                            required="true"
                            errorMessage="First Name is required."
                            v-model="userData['First Name']" />
                        </div>
                      
                        <div class="large-12 cell">
                          <InsInputWrap
                            id="userLastNameField"
                            name="userLastNameField" 
                            placeholder="Last Name"
                            label="Last Name*" 
                            field="text"            
                            autocomplete="off"
                            required="true"
                            errorMessage="Last Name is required."
                            v-model="userData['Last Name']" />                                     
                        </div>
                        <div class="large-12 cell">
                            <ins-input-tel 
                              label="Phone"
                              area-code="userDataAreaCode"
                              areacode-placeholder="Area Code"
                              phone-number="userDataPhoneNumber"
                              phonenum-placeholder="Phone Number"
                              ref="insInputTel"
                              v-bind:phonenum-value="phoneNumValue"
                              v-bind:areacode-value="areaCodeValue"
                              v-bind:readonly="formType === 'view'">
                            </ins-input-tel>
                        </div>
                        <div class="large-12 cell">
                          <InsInputWrap
                              id="userCompanyField"
                              name="userCompanyField" 
                              placeholder="Company"  
                              label="Company" 
                              field="company"               
                              autocomplete="off"
                              v-model="userData['Company']" />
                          
                        </div>
                      </div>
                    </ins-card>
                    <div class="grid-x grid-padding-x grid-padding-y">
                        <div class="large-12 cell text-right">
                            <ins-button type="button" id="cancelButtonDetails" label="Cancel" color="blue" outlined="true" size="normal" v-on:click='backToList()'></ins-button>
                            <ins-button type="submit" id="submitButtonDetails" v-bind:label="buttonLabel" color="blue" solid="true" size="normal" disabled="false"></ins-button>                            
                        </div>  
                    </div>            
                </form>

            <form id="userFormPasswordId" name="userFormPasswordId"
              class="large-5 medium-6 small-12 cell"
              action="#" 
              data-abide
              data-live-validate="true"
              novalidate onsubmit="event.preventDefault()"
              v-on:submit="changePassword">   
            <ins-card steady>
              <div class="grid-x grid-padding-x">
                <div class="large-12 cell">
                  <h4 class="ins-card-heading">Account Details</h4>
                    <InsInputWrap
                      id="userEmailField"
                      name="userEmailField" 
                      placeholder="Email"  
                      label="Email*" 
                      field="email"               
                      autocomplete="off"
                      required="true"
                      errorMessage="Email is invalid."
                      ref= "userEmailField"
                      disabled
                      v-model="userData['Email']" />                            
                </div>
                <div class="large-12 cell">
                    <InsInputWrap
                      id="userPasswordField"
                      name="userPasswordField" 
                      placeholder="Password"  
                      label="Password*" 
                      field="Password"               
                      autocomplete="off"
                      required="true"
                      errorMessage="Password is required."
                      v-model="password"
                      ref= "userPasswordField"
                      />  
                </div>
                <!-- <div class="large-12 cell">
                    <InsInputWrap
                      id="userConfirmPasswordField"
                      name="userConfirmPasswordField" 
                      placeholder="ConfirmPassword"  
                      label="Confirm Password*" 
                      field="Password"               
                      autocomplete="off"
                      required="true"
                      v-bind:has-error="password !== confirmPassword"
                      errorMessage="Password does not match."
                      v-model="confirmPassword"
                      ref= "userConfirmPasswordField"
                      />    
                </div> -->
              </div>              
            </ins-card>
            <div class="grid-x grid-padding-x grid-padding-y">
                <div class="large-12 cell text-right">
                    <ins-button type="submit" id="submitButtonDetails" label="Update Password" color="blue" solid="true" size="normal" disabled="false"></ins-button>                            
                </div>  
            </div>
            </form>

          </div>
        </div>       
    </div>  

    <div v-if="formType !== 'my-profile' && formType !== 'change-password'">
      <form id="userFormId" name="userForm"
        action="#" 
        data-abide
        data-live-validate="true"
        novalidate onsubmit="event.preventDefault()"
        v-on:submit="bindValues">
        
          <div class="grid-container-full">
            <div class="grid-x grid-padding-x">
              <div class="large-10 medium-12 small-12 cell">

                <ins-card steady>
                  <div class="grid-container-full">
                    <div class="grid-x grid-padding-x">

                      <div class="large-6 medium-6 small-12 cell">
                        <div class="grid-x grid-padding-x">                    
                          <div class="large-12 cell">
                          <h4 class="ins-card-heading">User Details</h4>
                              <InsInputWrap
                                id="userFirstNameField"
                                name="userFirstNameField" 
                                placeholder="First Name"  
                                label="First Name*" 
                                field="text"            
                                autocomplete="off"
                                required="true"
                                errorMessage="First Name is required."
                                v-model="userData['First Name']" />                
                          </div>
                        
                          <div class="large-12 cell">
                              <InsInputWrap
                                id="userLastNameField"
                                name="userLastNameField" 
                                placeholder="Last Name"
                                label="Last Name*" 
                                field="text"            
                                autocomplete="off"
                                required="true"
                                errorMessage="Last Name is required."
                                v-model="userData['Last Name']" />                                               
                          </div>
                          <div class="large-12 cell">
                            <ins-input-tel 
                              label="Phone"
                              area-code="userDataAreaCode"
                              areacode-placeholder="Area Code"
                              phone-number="userDataPhoneNumber"
                              phonenum-placeholder="Phone Number"
                              ref="insInputTel"
                              v-bind:phonenum-value="phoneNumValue"
                              v-bind:areacode-value="areaCodeValue"
                              v-bind:readonly="formType === 'view'">
                            </ins-input-tel>
                          </div>
                          <div class="large-12 cell">
                            <InsInputWrap
                                id="userCompanyField"
                                name="userCompanyField" 
                                placeholder="Company"  
                                label="Company" 
                                field="company"               
                                autocomplete="off"
                                v-model="userData['Company']" />
                            
                          </div>
                        </div>

                      </div>
                      
                      <div class="large-6 medium-6 small-12 cell">
                        <div class="grid-x grid-padding-x">
                          <div class="large-12 cell">
                            <h4 class="ins-card-heading">Account Details</h4>
                              <InsInputWrap
                                id="userEmailField"
                                name="userEmailField" 
                                placeholder="Email"  
                                label="Email*" 
                                field="email"               
                                autocomplete="off"
                                required="true"
                                errorMessage="Email is invalid."
                                ref= "userEmailField"
                                disabled
                                v-model="userData['Email']" />                            
                            </div>
                            <div v-if="formType === 'add'" class="large-12 cell">
                              <InsInputWrap
                                id="userPasswordField"
                                name="userPasswordField" 
                                placeholder="Password"  
                                label="Password*" 
                                field="Password"               
                                autocomplete="off"
                                required="true"
                                errorMessage="Password is required."
                                v-model="password"
                                ref= "userPasswordField"
                                />                            
                            </div>                                              
                        </div>
                      </div>

                    </div>
                  </div>
                </ins-card>
              </div>
            </div>
          </div>
        <div class="grid-container-full">
          <div class="grid-x grid-padding-x grid-padding-y">
            <div class="large-10 text-right cell submit-btn-wrap inline">        
              <ins-button type="button" id="cancelButtonDetails" label="Cancel" color="blue" outlined="true" size="normal" v-on:click='backToList()'></ins-button>
              <ins-button type="submit" id="submitButtonDetails" v-if="formType !== 'view'" v-bind:label="buttonLabel" color="blue" solid="true" size="normal" disabled="false"></ins-button>
              <ins-button type="button" id="saveAddMoreButtonDetails" v-if="formType == 'add'" label="Save and Create Another" color="blue" solid="true" size="normal" v-on:click="saveAddMore()" disabled="false"></ins-button>
            </div>
          </div>
        </div>
        </form>

    </div>
</div>
</template>

<script>
import router from "../router";
import moment from "moment";

import InsInputWrap from "../components/InsInputWrap.vue";

export default {
  name: "ViewUserDetails",
  components: {
    InsInputWrap
  },

  data() {
    return {
      userData: {},
      formType: this.$store.state.formType,
      formMethod: "",
      formName: "",
      fromURL: "/api/users",
      authToken: "",
      location: "",
      userCountryCode: "au",
      password: "",
      confirmPassword: "",
      buttonLabel: "Create",
      addMore: false,
      toastMsg: "",
      toastOpt: {
        closeHtml: '<button><i class="icon-close-1"></i></button>',
        closeButton: true,
        timeOut: 0,
        extendedTimeOut: 0
      },
      sourceProfile: "/api/get-current-user-data.json",
      areaCodeValue: "",
      phoneNumValue: ""
    };
  },
  methods: {
    backToList() {
      router.push("/admin-users");
    },
    saveAddMore() {
      this.addMore = true;
      this.bindValues();
    },
    onInputHandler(event) {
      event.target.id === "userNameField";
    },
    // validate form
    formValidate(form) {
      let missing_fields = 0;
      let _self = this;

      Array.from(form.getElementsByClassName("ins-form-field")).forEach(
        function(_field) {
          if (
            _field.hasAttribute("required") === true ||
            _field.getAttribute("required") == "true"
          ) {
            missing_fields += _self.validateInput(_field);
          }
          if (_field.getAttribute("type") === "email") {
            missing_fields += _self.validateEmail(_field);
          }
        }
      );

      if (missing_fields > 0) return false;
      return true;
    },
    // validate input value
    validateInput(_field) {
      if (_field.value.trim() === "" || _field.value === null) {
        this.invalidFieldNotif(_field);
        return 1;
      } else {
        this.validFieldNotif(_field);
        return 0;
      }
    },
    // update material design to show error message
    invalidFieldNotif(_field) {
      _field.closest("ins-input").setAttribute("has-error", true);
    },
    // update material design to remove error message
    validFieldNotif(_field) {
      _field.closest("ins-input").removeAttribute("has-error");
    },
    // check if email is valid
    validateEmail(_field) {
      let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

      if (re.test(String(_field.value).toLowerCase()) == true) {
        this.validFieldNotif(_field);
        return 0;
      } else {
        this.invalidFieldNotif(_field);
        return 1;
      }
    },
    changePassword() {
      this.formType = "change-password";
      this.bindValues();
    },
    bindValues() {
      let data = {};
      let countryCode = this.$refs.insInputTel.getCountryData();
      // user selected country
      //countryCode = countryCode.iso2;
      countryCode = countryCode.dialCode;

      let form = "";
      if (this.formType === "change-password") {
        form = document.getElementById("userFormPasswordId");
        data = {
          form_configuration_name: this.formName,
          form: {
            email: this.userData["Email"],
            password: this.password
          }
        };
      }
      if (this.formType === "add") {
        form = document.getElementById("userFormId");
        data = {
          form_configuration_name: this.formName,
          form: {
            email: this.userData["Email"],
            first_name: this.userData["First Name"],
            last_name: this.userData["Last Name"],
            name:
              this.userData["First Name"] + " " + this.userData["Last Name"],
            password: this.password,
            profiles_attributes: {
              administrator_attributes: {
                properties_attributes: {
                  company: $("#userCompanyField").val()
                }
              },
              contact_number_attributes: {
                properties_attributes: {
                  work_phone_country_code: countryCode,
                  work_phone_area_code: document.getElementsByName(
                    "userDataAreaCode"
                  )[0].value,
                  work_phone_number: document.getElementsByName(
                    "userDataPhoneNumber"
                  )[0].value
                }
              }
            }
          }
        };
      } else {
        form = document.getElementById("userFormId");
        data = {
          form_configuration_name: this.formName,
          form: {
            email: this.userData["Email"],
            first_name: this.userData["First Name"],
            last_name: this.userData["Last Name"],
            name:
              this.userData["First Name"] + " " + this.userData["Last Name"],
            profiles_attributes: {
              administrator_attributes: {
                properties_attributes: {
                  company: $("#userCompanyField").val()
                }
              },
              contact_number_attributes: {
                properties_attributes: {
                  work_phone_country_code: countryCode,
                  work_phone_area_code: document.getElementsByName(
                    "userDataAreaCode"
                  )[0].value,
                  work_phone_number: document.getElementsByName(
                    "userDataPhoneNumber"
                  )[0].value
                }
              }
            }
          }
        };
      }

      if (this.formType === "add") {
        this.toastMsg = "User";
      } else if (this.formType === "change-password") {
        this.toastMsg = "Your Password";
      } else {
        // edit user
        this.addMore = false;
        this.toastMsg = "Your user";
      }

      if (this.formValidate(form)) {
        this.submitForm(data);
      }
    },
    submitForm(data) {
      let self = this;
      let method = "";
      let headers = {
        "X-Requested-With": "XMLHttpRequest",
        "X-CSRF-Token": this.authToken,
        Accept: "application/json",
        "Content-Type": "application/json"
      };

      let options = {
        headers,
        method: this.formMethod,
        data: JSON.stringify(data),
        success: response => {
          self.successHandler();
        },
        error: error => {
          self.errorHandler(error);
        }
      };

      if (this.formType == "change-password") {
        // if (this.password !== this.confirmPassword) {
        //   this.invalidFieldNotif(
        //     document.getElementsByName("userConfirmPasswordField")[0]
        //   );
        // } else {
          $.ajax(this.formURL, options);
        // }
      } else {
        $.ajax(this.formURL, options);
      }
    },
    successHandler() {
      parent.toastIt(
        "success",
        `${this.toastMsg} has been ${
          this.formType === "add" ? "created" : "updated"
        }.`,
        this.toastOpt
      );
      document.getElementById("userFormId").reset();
      this.userData["First Name"] = "";
      this.userData["Last Name"] = "";
      this.userData["Company"] = "";
      this.userData["Email"] = "";
      this.password = "";
      !this.addMore ? router.push("/admin-users") : "";
      this.addMore = false;
    },
    errorHandler(error) {
      let errorMsg = "";
      if (error.responseJSON.errors) {
        if (
          error.responseJSON.errors.email !== "undefined" ||
          error.responseJSON.errors.email !== null
        ) {
          errorMsg += error.responseJSON.errors.email
            ? "- Email " + error.responseJSON.errors.email[0] + "<br />"
            : "";
        }
        if (
          error.responseJSON.errors.password !== "undefined" ||
          error.responseJSON.errors.password !== null
        ) {
          errorMsg += error.responseJSON.errors.password
            ? "- Password " + error.responseJSON.errors.password[0]
            : "";
        }
      }
      this.enableSubmitButtons();

      parent.toastIt("error", errorMsg, this.toastOpt);
    },
    enableSubmitButtons() {
      document
        .getElementById("submitButtonDetails")
        .setAttribute("disabled", false);
      if (this.formType === "add") {
        document
          .getElementById("saveAddMoreButtonDetails")
          .setAttribute("disabled", false);
      }
      document
        .getElementById("cancelButtonDetails")
        .setAttribute("disabled", false);
    },
    disableSubmitButtons() {
      document
        .getElementById("submitButtonDetails")
        .setAttribute("disabled", true);
      document
        .getElementById("saveAddMoreButtonDetails")
        .setAttribute("disabled", true);
      document
        .getElementById("cancelButtonDetails")
        .setAttribute("disabled", true);
    },
    setPhoneValue() {
      let self = this;
      setTimeout(() => {
        $(".phone").intlTelInput(
          "setNumber",
          "+" + self.userData["Work Phone Country Code"]
        );
        self.areaCodeValue = self.userData["Work Phone Area Code"];
        self.phoneNumValue = self.userData["Work Phone Number"];
      }, 300);
    },
    setFormSettings() {
      let self = this;
      if (this.formType === "add") {
        this.buttonLabel = "Create";
        this.formMethod = "POST";
        this.formURL = "/api/users";
        this.formName = "admin_sign_up";
        this.userData = {};
        //this.$refs.userEmailField.disabled = false
        document.getElementsByName("userEmailField")[0].disabled = false;

      } else if (this.formType === "view") {

        this.buttonLabel = "Update";
        this.userData = this.$store.state.userData;
        this.formMethod, this.formURL, (this.formName = "");
        let form = document.getElementById("userFormId");
        let elements = form.elements;

        for (var i = 0, len = elements.length; i < len; ++i) {
          elements[i].readOnly = true;
          elements[i].disabled = true;
        }

        console.log('view this.userData', this.userData);
        this.setPhoneValue();
      } else if (this.formType === "edit") {
        this.buttonLabel = "Update";
        this.userData = self.$store.state.userData;
        this.formMethod = "PUT";
        this.formURL = `${this.location}/api/users/${this.userData.id}`;
        this.formName = "admin_update_profile";

        console.log('edit this.userData', this.userData);
        this.setPhoneValue();
      } else if (this.formType === "remove") {
        this.userData = this.$store.state.userData;
      } else if ( this.formType === "my-profile" || this.formType === "change-password") {
        this.getProfileData();
        this.buttonLabel = "Update Details";
        this.formMethod = "PUT";
        this.formName = "admin_update_profile";
      } else {
        router.push("/overview");
      }
    },
    getProfileData() {
      let self = this;
      this.$http.get(this.sourceProfile).then(response => {
        if (response) {
          let data = response.data.current_user;
          let tempObj = {};

          if (data) {
            tempObj = {
              id: data.id,
              "First Name": data.first_name,
              "Last Name": data.last_name,
              Name: data.first_name + " " + data.last_name,
              Email: data.email,
              Role: "Administrator",
              Company: data.administrator.company_name === null ? "" : data.administrator.company_name,
              "Work Phone Country Code": data.contact_number === null ? "" : data.contact_number.work_phone_country_code,
              "Work Phone Area Code": data.contact_number === null ? "" : data.contact_number.work_phone_area_code,
              "Work Phone Number": data.contact_number === null ? "" : data.contact_number.work_phone_number,
              "Last Activity": moment(data.updated_at).fromNow()
            };

            // populate userData
            self.userData = tempObj;
            self.formURL = `${self.location}/api/users/${self.userData.id}`;
            console.log('getProfileData this.userData', this.userData);
            self.setPhoneValue();
          }
        } else {
          // user is not logged in
          router.push("/overview");
        }
      });
    }
  },
  mounted() {
    setTimeout(() => {
      this.$store.state.userData["Work Phone Country Code"] =
        this.$store.state.userData["Work Phone Country Code"] === ""
          ? "61"
          : this.$store.state.userData["Work Phone Country Code"];

      this.setFormSettings();
    }, 300);

    if (window.location.hostname !== "localhost") {
      this.authToken = JSON.parse(
        document.getElementById("formConfig").innerHTML
      );
    } else {
      this.authToken =
        "+Qj2j1eQpuy1fFxW6atnOluUyHLbL5cwDYL14rhASm13C+nkIPB6A5Ajxf/Cf1xFveFpEKMUd812HpUhyiBShQ==";
      this.location = "https://insites-poc.staging-sydney.near-me.com";
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
ins-button + ins-button {
  margin-left: 20px;
}
.ins-card-heading {
  font-size: 24px;
  font-weight: 300;
  font-family: "Open Sans", sans-serif;
  margin-bottom: 20px;
}
.ins-select-wrap .ins-select-value-wrap i.icon-caret-down {
  font-size: 16px;
  position: absolute;
  right: 11px;
  top: calc(50% - 8px);
  color: #8c94a4;
}
</style>
