import Vue from 'vue';
import VueRouter from 'vue-router';
import axios from 'axios';
import VueAxios from 'vue-axios';

import App from './App.vue';
import router from "./router";
import store from "./store";

Vue.use(VueRouter)
Vue.use(VueAxios, axios);

Vue.config.productionTip = false
Vue.config.ignoredElements = [
  "ins-card",
  "ins-filter",
  "ins-filter-item",
  "ins-datepicker",
  "ins-sparkline",
  "ins-line-chart",
  "ins-searchbar",
  "ins-table",
  "ins-tr",
  "ins-th",
  "ins-th-checkbox",
  "ins-td",
  "ins-td-checkbox",
  "ins-action",
  "ins-action-item",
  "ins-loader",
  "ins-button",
  "ins-dropdown",
  "ins-bar-chart",
  "ins-radio",
  "ins-toggle-switch",
  "ins-input",
  "ins-base-table",
  "ins-input-tel"
];

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
