import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        userData: {},
        formType: ""     
    },
    mutations: { 
        pushUserData(state, user) {
            state.userData = user;
        },
        pushFormType(state, formAction) {
            state.formType = formAction;
        }
    }

});