import Vue from 'vue'
import Router from 'vue-router'
import Overview from './views/Overview.vue'
import ListUsers from './views/ListUsers.vue'
import ViewUserDetails from './views/ViewUserDetails.vue'
import MyProfile from './views/MyProfile.vue'

Vue.use(Router)

export default new Router({
  routes: [   
    {
      path: '/overview',
      name: 'overview',
      component: Overview
    },
    {
      path: '/admin-users',
      name: 'adminUsers',
      component: ListUsers
    },
    {
      path: '/manage-admin-user',
      name: 'manageAdminUser',
      component: ViewUserDetails
    },
    {
      path: '/my-profile',
      name: 'myProfile',
      component: MyProfile
    }
  ]
})
